            vm.showform = true;
            vm.allCategories = allCategories;
            vm.categoryObj = product.getNewProductCategoryObj();
            vm.addNewCategory = addNewCategory;
            vm.addCategory = addCategory;
            vm.updateCategory = updateCategory;
