(function () {

    'use strict';

    angular.module('app.core')
        .filter('selectFilter', selectFilter)
        .filter('selectFilterDropDown', selectFilterDropDown)
        .filter('selectedRows', selectedRows)
        .filter('uniqueByAttribute', uniqueByAttribute)
        .filter('range', range)
        .filter('secondsToDateTime', secondsToDateTime)
        .filter('utcToLocal', utcToLocal)
        .filter('plusOrMinus', function(){
            return function(input){
                input = input ? input : 0;
                return input > 0 ? input : '-'+input
            }
        })
        .filter('percentage', ['$filter', function ($filter) {
            return function (input, decimals) {
                var number = (input > 1) ? input : input*100;
                return $filter('number')(number, decimals) + '%';
            };
        }])

        .filter('positive', function() {
            return function(input) {
                if (!input) {
                    return 0;
                }

                return Math.abs(input);
            };
        });



    function utcToLocal($filter) {
        return function (utcDateString, format) {
            console.log('hello', utcDateString);
            if (!utcDateString) {
                return;
            }

            // append 'Z' to the date string to indicate UTC time if the timezone isn't already specified
            if (utcDateString.indexOf('Z') === -1 && utcDateString.indexOf('+') === -1) {
                utcDateString += 'Z';
            }

            return $filter('date')(utcDateString, format);
        };
    }

    function secondsToDateTime() {
        return function(seconds) {
            return new Date(1970, 0, 1).setSeconds(seconds);
        };
    }

    function selectFilter(){

        return function(items, search, bind) {
            console.log(search, items, bind);
            if (!search) {
                return items;
            }
            return items.filter(function(element, index, array) {
                if(element[bind]){
                    return element[bind].toLowerCase().indexOf(search.toLowerCase())===0;
                }
            });

        };
    }

    function selectFilterDropDown(){
        return function(items, search, bind) {
            if (!search) {
                return items;
            }
            return items.filter(function(element, index, array) {
                if(element[bind]){
                    return element[bind].toLowerCase().indexOf(search.toLowerCase())===0;
                }
            });

        };
    }

    function uniqueByAttribute(){
        return function(items,attribute){
            return _.uniqBy(items, attribute );
        }
    }

    function selectedRows(){
        return function(rows,rowsToRemove,bind){
            if(!rowsToRemove || rowsToRemove.length === 0){
                return rows;
            }
            return rows.filter(function(element,index,array){
                if(element[bind]){
                    //return true;
                    return ((rowsToRemove.indexOf(element[bind])) <= -1)
                }
            });
        }
    }

    function range(){
        return function(input, total) {
            total = parseInt(total);
            for (var i=0; i<total; i++)
                input.push(i);
            return input;
        };
    }

}());

(function () {

    'use strict';

    angular.module('app.core')
        .config(corsHandler)
        .run(restInterceptors);


    function corsHandler($httpProvider) {
        $httpProvider.defaults.withCredentials = true;
        $httpProvider.defaults.xsrfCookieName = 'csrftoken';
        $httpProvider.defaults.xsrfHeaderName = 'X-CSRFToken';
        $httpProvider.interceptors.push(function($cookies) {
            return {
                'request': function(config) {
                    config.headers['X-CSRFToken'] = $cookies.get('csrftoken');
                    return config;
                }
            };
        });
    }

    function restInterceptors(Restangular, httpStatus, $state, loaderModal, localStorageService) {

        Restangular.setErrorInterceptor(errorInterceptor);
        Restangular.addResponseInterceptor(responseInterceptor);
        Restangular.addFullRequestInterceptor(requestInterceptor);

        function requestInterceptor(element, operation, route, url, headers, params, httpConfig){
            var today = new Date();
            var timezoneHourDifference = (today.getTimezoneOffset()/60);
            var extendedHeader = {
                'Hour-Difference': timezoneHourDifference
            };
            return angular.extend(headers, extendedHeader);
        }


        function responseInterceptor(response,deffered,responseHandler){
            return response;
        }

        function errorInterceptor(response, deferred, responseHandler){
            if(response.status === httpStatus.FORBIDDEN) {
                var lastState = {
                    stateOnLogin: $state.current.name,
                    stateParamsOnLogin: $state.params
                };
                localStorageService.set('lastState',lastState);
                $state.go('login');
                loaderModal.close();
                return false;
            }
            return true;
        }

    }


}());

