(function(){

    'use strict';

    angular.module('app.core')
        .config(configuration)
        .run(routingEvents);

    /* @ngInject */
    function configuration($urlRouterProvider){
        $urlRouterProvider.otherwise('/login');
    }

    /* @ngInject */
    function routingEvents($rootScope){
        //on routing error
        $rootScope.$on('$stateNotFound', function(event, unfoundState, fromState, fromParams){
            //do some logging and toasting
        });

        //on routing error
        $rootScope.$on('$stateChangeSuccess', function(event, toState, toParams, fromState, fromParams){

            //do some title setting
            $rootScope.pageTitle = toState.title || 'Doni Enterprises';
            $rootScope.bgBackgroundClass = toState.bgBackgroundClass || '';
            if(toState.pageHeader){
                if(toState.pageHeader.title){
                    $rootScope.headerTitle = toState.pageHeader.title;
                }
                if(toState.pageHeader.subTitle){
                    $rootScope.headerSubTitle = toState.pageHeader.subTitle;
                }
                $rootScope.headerAnchor = (toState.pageHeader.headerAnchor) ? toState.pageHeader.headerAnchor : [];
                $rootScope.goBack = toState.pageHeader.goBack || false;
            }
            else{
                $rootScope.headerAnchor = [];
                $rootScope.goBack = false;
            }
            fromState.wentTo = toState.name;
            toState.params = toParams;
            if(toState.wentTo !== fromState.name){

                toState.prevState = fromState.name;
                toState.prevParam = fromParams;
            }
        });
    }

}());

(function () {

    'use strict';

    angular.module('app.core')
        .config(selectizeDecorator);


    function selectizeDecorator($provide){
        $provide.decorator('selectizeDirective', function($delegate, selectizeConfig) {
            var directive = $delegate[0];

            var link = myLinkFnOverride;

            function myLinkFnOverride(scope, element, attrs, modelCtrl) {
                var selectize,
                    settings = angular.extend({}, Selectize.defaults, selectizeConfig, scope.config);

                scope.options = scope.options || [];
                scope.config = scope.config || {};

                var toggle = function(disabled) {
                    disabled ? selectize.disable() : selectize.enable();
                };

                var isEmpty = function (val) {
                    return (val === undefined) || (val === null) || ((val.constructor === Array) && !val.length); //support checking empty arrays
                };

                var validate = function validate() {
                    var isInvalid = (scope.ngRequired() || attrs.required || settings.required) && !isEmpty(scope.ngModel);
                    modelCtrl.$setValidity('required', isInvalid);
                };

                var setSelectizeOptions = function(curr, prev) {
                    angular.forEach(prev, function(opt){
                        if(curr.indexOf(opt) === -1){
                            var value = opt[settings.valueField];
                            selectize.removeOption(value, true);
                        }
                    });

                    selectize.addOption(curr, true);

                    setSelectizeValue();
                }

                var setSelectizeValue = function() {
                    validate();

                    selectize.$control.toggleClass('ng-valid', modelCtrl.$valid);
                    selectize.$control.toggleClass('ng-invalid', modelCtrl.$invalid);
                    selectize.$control.toggleClass('ng-dirty', modelCtrl.$dirty);
                    selectize.$control.toggleClass('ng-pristine', modelCtrl.$pristine);

                    if (!angular.equals(selectize.items, scope.ngModel)) {
                        selectize.setValue(scope.ngModel, true);
                    }
                }

                settings.onChange = function(value) {
                    var value = angular.copy(selectize.items);
                    if (settings.maxItems == 1) {
                        value = value[0]
                    }
                    modelCtrl.$setViewValue( value );

                    if (scope.config.onChange) {
                        scope.config.onChange.apply(this, arguments);
                    }
                };

                settings.onOptionAdd = function(value, data) {
                    if( scope.options.indexOf(data) === -1 ) {
                        scope.options.push(data);

                        if (scope.config.onOptionAdd) {
                            scope.config.onOptionAdd.apply(this, arguments);
                        }
                    }
                };

                settings.onInitialize = function() {
                    selectize = element[0].selectize;

                    setSelectizeOptions(scope.options);

                    //provides a way to access the selectize element from an
                    //angular controller
                    if (scope.config.onInitialize) {
                        scope.config.onInitialize(selectize);
                    }

                    scope.$watchCollection('options', setSelectizeOptions);
                    scope.$watch('ngModel', setSelectizeValue);
                    scope.$watch('ngDisabled', toggle);
                };

                element.selectize(settings);

                element.on('$destroy', function() {
                    if (selectize) {
                        selectize.destroy();
                        element = null;
                    }
                });
            }

            directive.compile = function() {
                return function(scope, element, attrs) {
                    link.apply(this, arguments);
                };
            };

            return $delegate;
        });
    }

}());
