(function() {

    'use strict';

    angular
        .module('app.widgets')
        .directive('tradeTable', tradeTable);

    /* @ngInject */
    function tradeTable(appConstants, tradeBook, toastr, appFormats, utilities) {
        return {
            link: link,
            restrict: 'E',
            templateUrl: 'app/widgets/tradeTable/tradeTable.template.html',
            scope: {
                type:'@',
                listData: '=',
                itemsPerPage: '@'
            },
            replace: true
        };

        function link(scope){
            _init();
            scope.toggleNotes = function(row){
                if(row.showNotes){
                    row.showNotes = false;
                }
                else{
                    row.showNotes = true;
                    row.noteListLoading = true;
                    tradeBook.getAllNotesForTrade(row.id).then(function(res){
                        row.noteList = res.notesList;
                        row.noteListLoading = false;
                    });
                    angular.forEach(scope.listData, function(val,key){
                        if(val.id !== row.id){
                            val.showNotes = false;
                        }
                    });
                }

            };

            scope.editTradeNote = function(trade, noteText, note){
                trade.note = noteText;
                note.editing = true;
                scope.updateNote = true;
            };

            scope.deleteTradeNote = function(tradeId, noteId, note, noteList){
                return tradeBook.deleteTradeNote(tradeId, noteId).then(function(res){
                    if(res.success){
                        var index = _.indexOf(noteList, note);
                        noteList.splice(index, 1);
                        toastr.success(res.message, 'Note Deleted');
                    }
                    else{
                        toastr.error(res.message);
                    }
                });
            };

            scope.addTradeNote = function(tradeId, note, noteList, trade, formObj){
                if(formObj.$valid){
                    trade.note = '';
                    if(note === ''){
                        toastr.error('Please add a note');
                    }
                    else{
                        scope.addingNote = false;
                        if(!scope.updateNote){
                            tradeBook.addTradeNote(tradeId, note).then(function(res){
                                if(res.success){
                                    toastr.success(res.message, 'Note Added');
                                    noteList.push(res.note);
                                }
                                else{
                                    toastr.error(res.message);
                                }
                                utilities.resetFormValidation(formObj);
                            })
                        }
                        else{
                            var noteEdited = _.find(noteList, function(n) { return (n.editing == true); });
                            var index = _.indexOf(noteList, noteEdited);
                            tradeBook.editTradeNote(tradeId, noteEdited.noteId, note).then(function(res){
                                if(res.success){
                                    scope.updateNote = false;
                                    noteList[index] = res.note;
                                    toastr.success(res.meesage, 'Note Updated')
                                }
                                else{
                                    toastr.error(res.message);
                                }
                                utilities.resetFormValidation(formObj);


                            })
                        }

                    }
                }

            };

            function _init(){
                scope.searchTransactionByFileID = '';
                scope.displayList = [];
                scope.appConstants = appConstants;
                scope.appFormats = appFormats;
                scope.currentPage = 1;
                var intTradeHeading = [
                    'Date',
                    'File No',
                    'Buyer',
                    'Product',
                    'Origin',
                    'Quantity',
                    'Rate',
                    'Seller',
                    'Shipment Start',
                    'Shipment End',
                    'Expected Commission',
                    'Actual Commission'
                ];
                scope.heading = intTradeHeading;
                angular.forEach(scope.listData, function(val,key){
                    val.showNotes = false;
                    val.note = '';
                });

                scope.pageChanged = function(page){
                    scope.setPagingData(page, scope.listData, scope.itemsPerPage);
                };

                scope.setPagingData = function(page, list, itemsPerPage) {
                    scope.totalItems = list.length;
                    scope.currentPage = page;
                    var pagedData = list.slice((page - 1) * itemsPerPage, page * itemsPerPage);
                    scope.displayList = pagedData;
                };

                scope.$watch('listData', function(newVal, oldVal){
                    if(newVal.length > 0) {
                        scope.setPagingData(scope.currentPage, newVal, scope.itemsPerPage);
                    }
                });
            }

        }
    }
})();
(function() {

    'use strict';

    angular
        .module('app.widgets')
        .directive('tradeWashOutCard', tradeWashOutCard);

    /* @ngInject */
    function tradeWashOutCard($filter, tradeBook, toastr, appFormats, utilities, loaderModal) {
        return {
            link: link,
            restrict: 'E',
            templateUrl: 'app/widgets/tradeWashOutCard/tradeWashOutCard.template.html',
            scope: {
                trade:'='
            },
            replace: true
        };

        function link(scope){
            _init();

            function _init(){
                scope.appFormats = appFormats;
                if(!scope.trade.washOut){
                    scope.trade.washOut = getNewWashoutObj();
                }
                scope.getNewWashoutObj = getNewWashoutObj;
                scope.showWashOutForm = showWashOutForm;
                scope.saveWashOutDetails = saveWashOutDetails;
                scope.cancelWashingOut = cancelWashingOut;
            }


            function cancelWashingOut(){
                scope.showForm = false;
            }


            function getNewWashoutObj(){
                return {
                    'isWashout': false,
                    'initialCommissionPayable': true,
                    'washoutDate': null,
                    'washoutDueDate': null,
                    'buyerWashoutPrice': $filter('number')(scope.trade.commission.price, '2'),
                    'sellerWashoutPrice': $filter('number')(scope.trade.commission.price, '2'),
                    'brokerDifference': $filter('number')('0.00', '2')
                }
            }

            function showWashOutForm(action){
                // scope.washOutValue = (action==='add') ? scope.trade.commission.price : scope.trade.washOut.isWashOutAt;
                // scope.washOutValue = $filter('number')(scope.washOutValue, 2);
                scope.addMode = true;
                scope.showForm = true;
            }

            function saveWashOutDetails(washOutForm, trade, status, washOutValue, activate){
                if(washOutForm.$valid || !activate){
                    loaderModal.open();
                    tradeBook.changeWashoutStatus(trade.id, status, washOutValue).then(function(res){
                        if(res.success){
                            scope.trade = res.transactionObj;
                            toastr.success(res.message, 'Washout Status Changed');
                            scope.showForm = false;
                            if(!status){
                                scope.trade.washOut = getNewWashoutObj();
                            }
                        }
                        else{
                            toastr.error(res.message, 'Error!');
                        }
                        loaderModal.close();
                    })
                }
                else{
                    toastr.error('Please enter washout value', 'Error')
                }

            }
        }
    }

})();
