(function() {
  'use strict';

  angular.module('app.theme.components')
    .controller('baWizardCtrl', baWizardCtrl);

  /** @ngInject */
  function baWizardCtrl($scope, toastr) {
    var vm = this;

    vm.tabs = [];
    vm.tabNum = 0;
    vm.progress = 0;

    vm.addTab = function(tab) {
      tab.setPrev(vm.tabs[vm.tabs.length - 1]);
      vm.tabs.push(tab);
      vm.tabNum = 0;
      vm.tabs.forEach(function (t, tIndex) {
        tIndex == 0 ? t.select(true) : t.select(false);
      });
    };

    vm.onCancel = function(){
      $scope.onCancel({});
    };

    $scope.$watch(angular.bind(vm, function () {return vm.tabNum;}), calcProgress);

    vm.selectTab = function (tabNum) {
      var promise = vm.tabs[vm.tabNum].submit();
      if(promise){
        promise.then(function(res){
          if(res.success){
            if(vm.tabs[vm.tabNum].onSubmitCallback){
              vm.tabs[vm.tabNum].onSubmitCallback({'response':res});
            }
            if (vm.tabs[tabNum+1].isAvailiable()) {
              vm.tabNum = tabNum+1;
              vm.tabs.forEach(function (t, tIndex) {
                tIndex == vm.tabNum ? t.select(true) : t.select(false);
              });
            }
            toastr.success(res.message);
          }
          else{
            toastr.error(res.message);
          }
        });
      }

    };

    vm.isFirstTab = function () {
      return vm.tabNum == 0;
    };

    vm.isLastTab = function () {
      return vm.tabNum == vm.tabs.length - 1 ;
    };

    vm.nextTabAndExit = function(){
      vm.tabs[vm.tabNum].submit().then(function(res){
        if(res.success){
          toastr.success(res.message);
          $scope.onCancel({});
        }
        else{
          toastr.error(res.message);
        }
      });
    };

    vm.nextTab = function () {
      vm.selectTab(vm.tabNum)
    };

    vm.previousTab = function () {
      vm.tabs[vm.tabNum].select(false);
      vm.tabNum = vm.tabNum - 1;
      vm.tabs[vm.tabNum].select(true);
    };

    function calcProgress() {
      vm.progress = ((vm.tabNum + 1) / vm.tabs.length) * 100;
    }
  }
})();


(function() {
  'use strict';

  angular.module('app.theme.components')
    .directive('baWizardStep', baWizardStep);

  /** @ngInject */
  function baWizardStep(toastr) {
    return {
      restrict: 'E',
      transclude: true,
      require: '^baWizard',
      scope: {
        form: '=',
        submitButtonTitle: '@',
        skip: '=',
        onNext: '&',
        onNextCallback: '&'
      },
      templateUrl:  'app/theme/components/baWizard/baWizardStep.html',
      link: function($scope, $element, $attrs, wizard) {
        $scope.selected = true;

        var tab = {
          onSubmitCallback: $scope.onNextCallback ? $scope.onNextCallback : null,
          title: $attrs.title,
          submitButtonTitle: $scope.submitButtonTitle,
          select: select,
          submit: submit,
          isComplete: isComplete,
          isAvailiable: isAvailiable,
          prevTab: undefined,
          setPrev: setPrev
        };

        wizard.addTab(tab);

        function select(isSelected) {
          if(isSelected) {
            $scope.selected = true;
          } else {
            $scope.selected = false;
          }
        }

        function submit() {
          console.log('hello');
          $scope.form && $scope.form.$setSubmitted(true);
          if($scope.form && $scope.form.$valid){
            return $scope.onNext({form: $scope.form});
          }
          else if($scope.form){
            toastr.error('Please enter missing fields', 'Invalid Form')
          }
        }

        function isComplete() {
          return $scope.form ? $scope.form.$valid : true;
        }

        function isAvailiable() {
          return tab.prevTab ? tab.prevTab.isComplete() : true;
        }

        function setPrev(pTab) {
          tab.prevTab = pTab;
        }
      }
    };
  }
})();
